var wx = require("./jweixin.js");
var imageUrl = "http://www.li-li.cn/app/icon.png";
var wxConfig = {

    wxConfig: function (type) {
        var url = window.location.href;
        var urlArr = url.split("#");
        var noncestr = "";
        var signature = "";
        var timestamp = "";
        $.ajax({
            type: "get",
            url: "http://www.li-li.cn/llwx/wx/jsOauth",
            data: {
                url: urlArr[0]
            },
            dataType: "json",
            async: false,
            success: function (data) {
                if (data.code == 0) {
                    console.log(data);
                    var data = data.data;
                    noncestr = data.noncestr;
                    signature = data.signature;
                    timestamp = data.timestamp;
                }
            }
        });
        wx.config({
            debug: false,
            //appId: "wx82c10b61c95e9f30",//
            appId: "wxd8c1d6ab5eb3c981",//
            timestamp: timestamp,//
            nonceStr: noncestr,//
            signature: signature,//
            jsApiList: [
                'onMenuShareTimeline',
                'onMenuShareAppMessage',
                'hideAllNonBaseMenuItem',
                'showMenuItems',
                'getLocation'
            ]
        });

        wx.ready(function () {
            //
            wx.hideAllNonBaseMenuItem();
        })
    },
    wxShare: function (title, desc, link) {
        wx.showMenuItems({
            menuList: ["menuItem:share:appMessage", "menuItem:share:timeline"] // menu3
        });
        //""
        wx.onMenuShareAppMessage({
            title: title,
            desc: desc,
            link: link,
            imgUrl: imageUrl,
            type: "link",//musicvideolinklink
        });
        //""
        wx.onMenuShareTimeline({
            title: title,
            link: link,
            imgUrl: imageUrl
        });
    },
    getWx: function () {
        return wx;
    },

    wxLocation: function () {
        wx.getLocation({
            success: function (res) {
                return res.latitude + "," + res.longitude;
            },
            cancel: function (res) {
                //alert('');
            }
        });
    },
}

module.exports = wxConfig;


